<template>
	<div id="deliveryer-reward">
		<public-header title="打赏骑手"></public-header>
		<div class="content font-14">
			<div class="deliveryer">
				<div class="flex-lr">
					<div class="flex">
						<div class="avatar">
							<img class="img-100" :src="deliveryer.avatar" >
						</div>
						<div>
							<div class="realname">{{deliveryer.title}}</div>
							<div class="plateform" v-if="deliveryer.delivery_title">{{deliveryer.delivery_title}}</div>
						</div>
					</div>
					<span class="icon icon-telephone font-16 font-bold" @click="util.jsTel(deliveryer.mobile)"></span>
				</div>
				<van-row class="text-center margin-15-t">
					<van-col span="8">
						<div class="van-hairline--left">
							<div>
								{{deliveryer.percent_normal}}
								<span class="font-12">%</span>
							</div>
							<div class="data-title">准时率</div>
						</div>
					</van-col>
					<van-col span="8">
						<div class="van-hairline--left">
							<div>
								{{deliveryer.score}}
								<span class="font-12">分</span>
							</div>
							<div class="data-title">评分</div>
						</div>
					</van-col>
					<van-col span="8">
						<div class="van-hairline--left">
							<div>
								{{deliveryer.avg_delivery_time}}
								<span class="font-12">分钟</span>
							</div>
							<div class="data-title">平均配送时长</div>
						</div>
					</van-col>
				</van-row>
			</div>
			<div class="reward">
				<div class="font-bold">骑手配送辛苦，快打个赏吧</div>
				<div class="font-12 c-gray margin-5-t">打赏将全部转给骑手，平台不收任何费用</div>
				<van-row gutter="10" class="margin-5-t margin-5-b">
					<van-col span="6" v-for="(item, index) in award.items" :key="index">
						<div :class="{'reward-item': 1, active: index == selectIndex}" @click="onChangeFee(index, item.fee)">
							<div class="reward-img">
								<img class="img-100" :src="item.imgurl">
							</div>
							<div class="reward-fee">
								<span class="font-12">{{Lang.dollarSign}}</span><span>{{item.fee}}</span>
							</div>
						</div>
					</van-col>
					<van-col span="6">
						<div :class="{'reward-item': 1, active: selectIndex == 'diy'}" @click="onTogglePopup('diy', false)">
							<div class="reward-img">
								<img class="img-100" src="static/img/svip-redpacket.png">
							</div>
							<div class="reward-fee">
								<template v-if="selectIndex == 'diy' && input > 0">
									<span class="font-12">{{Lang.dollarSign}}</span><span>{{input}}</span>
								</template>
								<template v-else>
									<span>其他金额</span>
								</template>
							</div>
						</div>
					</van-col>
				</van-row>
				<div class="tip ellipsis" v-if="tip">
					<span class="c-danger">捎句话：</span>
					<span>{{tip}}</span>
				</div>
				<van-button size="normal" block type="danger" class="margin-10-t" @click="onSubmit" :disabled="!islegal">确认打赏</van-button>
				<div class="person-num" v-if="deliveryer.award_total > 0">
					<div class="avatar">
						<img class="img-100" :src="deliveryer.award_avatar" alt="">
					</div>
					<div>{{deliveryer.award_total}}人打赏过</div>
					<div class="icon icon-right font-12 c-gray"></div>
				</div>
			</div>
		</div>
		<van-popup v-model="popup.diy" class="popup-diy">
			<div class="title">多少都是您的心意</div>
			<div class="input-wrap">
				<input v-model.number="input" :placeholder="`最低${award.diy_min}${Lang.dollarSignCn}, 最高${award.diy_max}${Lang.dollarSignCn}`" type="text">
				<span class="c-gray">{{Lang.dollarSignCn}}</span>
			</div>
			<van-row gutter="10">
				<van-col span="12">
					<van-button size="normal" block @click="onTogglePopup('diy', false)">取消</van-button>
				</van-col>
				<van-col span="12">
					<van-button size="normal" type="danger" block @click="onTogglePopup('diy', true)">确定</van-button>
				</van-col>
			</van-row>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getData, setData} from '@/controller/funcCommon'
	export default {
		components: {
			PublicHeader
		},
		data() {
			return {
				id: 0,
				deliveryer: {},
				award: {
					status: 0,
					diy_status: 0,
					diy_min: 0,
					diy_max: 10,
					day: 0,
					items: []
				},
				fee: 0,
				selectIndex: -1,
				input: '',
				tip: '',
				islegal: false,
				popup: {
					diy: false
				},
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'wmall/order/index/award',
					data: {
						id: that.id
					},
					success: (res) => {
						that.deliveryer = that.util.extend(that.deliveryer, res.deliveryer);
						that.award = that.util.extend(that.award, res.award);
						that.islegal = true;
					}
				})
			},

			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				var fee = parseFloat(that.fee);
				if(isNaN(fee) || fee <= 0) {
					that.util.$toast('请选择或输入打赏金额');
					return false;
				}
				if(that.award.diy_status == 1 && that.selectIndex == 'diy' && (fee > that.award.diy_max || fee < that.award.diy_min)) {
					that.util.$toast('请输入打赏金额，最少' + that.award.diy_min + that.Lang.dollarSignCn + '，最多' + that.award.diy_max + that.Lang.dollarSignCn);
					return false;
				}
				that.islegal = false;
				setData({
					vue: that,
					url: 'wmall/order/index/award',
					data: {
						id: that.id,
						fee: fee
					},
					success: (res) => {
						that.$router.replace(that.util.getUrl({path: '/pages/public/pay', query: {order_id: res, order_type: 'deliveryer_award'}}));
					},
					fail: (res) => {
						that.islegal = true;
						that.util.$toast(res);
					}
				})
			},

			onChangeFee(index, fee) {
				var that = this;
				that.selectIndex = index;
				that.fee = fee;
				that.tip = that.award.items[index].tip;
				that.input = '';
			},

			onTogglePopup(key, force) {
				var that = this;
				var status = that.popup[key];
				if(key == 'diy' && force) {
					var input = parseFloat(that.input);
					if(isNaN(input) || input <= 0 || input > that.award.diy_max) {
						that.util.$toast('请输入打赏金额，最少' + that.award.diy_min + that.Lang.dollarSignCn + '，最多' + that.award.diy_max + that.Lang.dollarSignCn);
						return false;
					}
					that.fee = input;
					that.selectIndex = 'diy';
					that.tip = that.award.diy_tip;
				}
				that.popup[key] = !status;
			}
		},
		mounted() {
			let query = this.util.parseQuery(this.$route.query);
			this.id = query.id;
			this.onLoad();
		}
	}
</script>

<style>
	#deliveryer-reward .content{
		background: linear-gradient(to bottom, #ff2d4b, #f5f5f5) no-repeat;
		background-size: 100% 180px;
	}
	#deliveryer-reward .deliveryer{
		margin: 15px;
		padding: 15px;
		border-radius: 10px;
		background-color: #fff;
	}
	#deliveryer-reward .deliveryer .avatar{
		width: 50px;
		height: 50px;
		border-radius: 5px;
		overflow: hidden;
		font-size: 0px;
		margin-right: 10px;
	}
	#deliveryer-reward .deliveryer .realname{
		font-size: 15px;
		font-weight: bold;
	}
	#deliveryer-reward .deliveryer .plateform{
		background-color: #ff2d4b;
		color: #fff;
		font-size: 12px;
		padding: 3px 10px;
		border-radius: 5px 0;
		margin-top: 5px;
	}
	#deliveryer-reward .data-title{
		font-size: 12px;
		color: #999;
		margin-top: 7px;
	}
	#deliveryer-reward .reward{
		margin: 15px;
		padding: 15px;
		border-radius: 10px;
		background-color: #fff;
	}
	#deliveryer-reward .reward .van-col{
		padding: 5px 0;
	}
	#deliveryer-reward .reward .reward-item{
		background-color: #f5f5f5;
		border: .5px solid #f5f5f5;
		border-radius: 5px;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		padding: 20px 0;
	}
	#deliveryer-reward .reward .reward-img{
		max-width: 10vw;
		height: 10vw;
		margin-bottom: 10px;
		font-size: 0px;
	}
	#deliveryer-reward .reward .reward-item.active{
		background-color: #fff;
		border: .5px solid #ff2d4b;
	}
	#deliveryer-reward .reward .reward-item.active .reward-fee{
		color: #ff2d4b;
	}
	#deliveryer-reward .reward .tip{
		padding: 10px;
		border-radius: 5px;
		font-size: 12px;
		background-color: #f5f5f5;
	}
	#deliveryer-reward .reward .person-num{
		margin-top: 10px;
		color: #999;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#deliveryer-reward .reward .person-num .avatar{
		width: 16px;
		height: 16px;
		border-radius: 3px;
		overflow: hidden;
		margin-right: 5px;
	}
	#deliveryer-reward .popup-diy{
		width: 80%;
		padding: 15px;
		border-radius: 10px;
	}
	#deliveryer-reward .popup-diy .title{
		text-align: center;
		font-size: 16px;
		font-weight: bold;
	}
	#deliveryer-reward .input-wrap{
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 10px;
		margin: 20px 0;
		border-radius: 5px;
		background-color: #f5f5f5;
	}
	#deliveryer-reward .input-wrap input{
		background-color: #f5f5f5;
		flex: 1;
		margin-right: 10px;
		font-size: 14px;
	}
</style>


